import { mapState, mapActions } from 'vuex'
import axios from 'axios'
import url from '../../../urls/index.js'

export default {
  name: 'empresas-param',
  components: {},
  props: [],
  data() {
    return {
      snackbarOn: false,
      snackbarAgregar: false,
      opExitosa: false,
      opAgregarExitosa: false,
      isFormValid: false,
      //El id empresa debe ser checkeado contra la tabla empresa
      reglasEmpresa: [
        value => (/^\d+$/.test(value) && value > 0 && value <= 9999) || 'Id Empresa debe ser un número entre 1 y 9999',
        value => this.empresas.find(empresa => empresa.id_empresa == value) != null || `El id_empresa ${value} no existe`,
      ],
      //El id tabla debe ser checkeado contra la tabla p_codigos_tabla
      reglasTabla: [
        value => (/^\d+$/.test(value) && value > 0 && value <= 9999) || 'Id Tabla debe ser un número entre 1 y 9999',
        value => this.codigosTabla.find(tabla => tabla.id_tabla == value) != null || `El id_tabla ${value} no existe`,
      ],
      reglasIdOpcion: [
        value => (/^\d+$/.test(value) && value > 0 && value <= 9999) || 'Id Opcion debe ser un número entre 1 y 9999',
      ],
      reglasVisible: [
        value => !!value || 'El campo Visible es requerido!',
        value => (/^\d+$/.test(value) && ( value == 0 || value == 1)) || 'El cambo Visible debe ser 0 o 1'
      ],
      reglasBloqueante: [
        value => !!value || 'El campo Bloqueabte es requerido!',
        value => (/^\d+$/.test(value) && ( value == 0 || value == 1)) || 'El cambo Bloqueante debe ser 0 o 1'
      ],
      headers: [
        {
          text: "Id_empresa",
          value: "id_empresa"
        },
        {
          text: "Id_tabla",
          value: "id_tabla"
        },
        {
          text: "Id_opcion",
          value: "id_opcion"
        },
        {
          text: "Visible",
          value: "visible"
        },
        {
          text: "bloqueante",
          value: "bloqueante"
        },
        {
          text: 'Acciones',
          value: 'actions',
          sortable: false
        }
      ],
      itemsPorPagina: 10,
      dialog: false,
      editedIndex: -1,
      editedItem: {
        id_empresa: '',
        id_tabla: '',
        id_opcion: '',
        visible: '',
        bloqueante: '',
      },
      defaultItem: {
        id_empresa: '',
        id_tabla: '',
        id_opcion: '',
        visible: '',
        bloqueante: '',
      }
    }
  },

  computed: {

    colorSnackbar() {
      return this.opExitosa ? 'success' : 'error'
    },

    colorSnackbarAgregar() {
      return this.opAgregarExitosa ? 'success' : 'error'
    },

    ...mapState('auth', ['token']),
    ...mapState('adminTipos', ['tiposEmpresasParam', 'codigosTabla']),
    ...mapState('empresas', ['empresas']),

    formTitle() {
      return this.editedIndex === -1 ? 'Agregar Empresas Param' : 'Editar Empresas Param'
    },

  },


  watch: {
    dialog(val) {
      val || this.close()
    },
  },

  mounted() {
    this.obtenerCodigosTabla()
    this.obtenerTiposEmpresasParam()
    this.obtenerEmpresas()
  },

  methods: {
    ...mapActions('adminTipos', ['obtenerTiposEmpresasParam', 'obtenerCodigosTabla']),
    ...mapActions('empresas', ['obtenerEmpresas']),

    cerrarSnackbar() {
      this.snackbarOn = false,
      this.snackbarAgregar = false
      this.opExitosa = false
      this.opAgregarExitosa = false
    },

    editItem(item) {
      this.editedIndex = this.tiposEmpresasParam.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })

    },

    async save() {
      if (this.editedIndex > -1) {
        await this.actualizarEmpresasParam(this.editedItem)
        this.snackbarOn = true
      } else if(this.tiposEmpresasParam.find(emp => emp.id_empresa == this.editedItem.id_empresa && emp.id_tabla == this.editedItem.id_tabla && emp.id_opcion == this.editedItem.id_opcion) == null){
        await this.agregarEmpresasParam(this.editedItem)
        this.snackbarAgregar = true
      } else {
        this.snackbarAgregar = true
      }
      this.close()
      setTimeout(this.cerrarSnackbar, 3100)
    },

    async actualizarEmpresasParam(aux) {
      try {
        let header = { 'token': this.token }
        let configuracion = { headers: header }
        await axios.put(url.empresasParam.urlModificar + '/' + aux.id_empresa + '/' + aux.id_tabla + '/'+ aux.id_opcion, aux, configuracion)
        await this.obtenerTiposEmpresasParam()
        this.opExitosa = true
      } catch (error) {
        console.log("Error GET: " + error)
      }
    },

    async agregarEmpresasParam(aux) {
      try {
        let header = { 'token': this.token }
        let configuracion = { headers: header }
        await axios.post(url.empresasParam.urlAgregar, aux, configuracion)
        await this.obtenerTiposEmpresasParam()
        this.opAgregarExitosa = true
      } catch (error) {
        console.log("Error GET: " + error)
      }
    },

  }

}