import { mapState, mapActions } from 'vuex'
import axios from 'axios'
import url from '../../../urls/index.js'

export default {
  name: 'tipos-transaccion',
  components: {},
  props: [],
  data() {
    return {
      snackbarOn: false,
      opExitosa: false,
      isFormValid: false,
      reglasID: [
        value => (/^\d+$/.test(value) && value > 0 && value <= 9999) || 'Id Tipos de Transaccion debe ser un número entre 1 y 9999',
        value => !this.tiposTransaccion.find(tipo => tipo.id_tipo_transaccion == value) || `El ID ${value} ya fue utilizado`,
      ],
      reglasDescripcion: [
        value => !!value || 'El campo Descripción es requerido!'],
      headers: [
        {
          text: "Id tipo transaccion",
          value: "id_tipo_transaccion"
        },
        {
          text: "Descripcion",
          value: "descripcion"
        },
        {
          text: "Aux1",
          value: "aux1"
        },
        {
          text: "Aux2",
          value: "aux2"
        },
        {
          text: 'Acciones',
          value: 'actions',
          sortable: false
        }
      ],
      itemsPorPagina: 10,
      dialog: false,
      editedIndex: -1,
      editedItem: {
        id_tipo_transaccion: '',
        descripcion: '',
        aux1: '',
        aux2: '',
      },
      defaultItem: {
        id_tipo_transaccion: '',
        descripcion: '',
        aux1: '',
        aux2: '',
      }
    }
  },

  computed: {

    colorSnackbar() {
      return this.opExitosa ? 'success' : 'error'
    },

    ...mapState('auth', ['token']),
    ...mapState('adminTipos', ['tiposTransaccion']),

    formTitle() {
      return this.editedIndex === -1 ? 'Agregar Tipo de Transaccion' : 'Editar Tipo de Transaccion'
    },

  },


  watch: {
    dialog(val) {
      val || this.close()
    },
  },

  mounted() {
    this.obtenerTiposTransaccion()
  },

  methods: {
    ...mapActions('adminTipos', ['obtenerTiposTransaccion']),

    cerrarSnackbar() {
      this.snackbarOn = false,
      this.opExitosa = false
    },

    editItem(item) {
      this.editedIndex = this.tiposTransaccion.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })

    },

    async save() {
      if (this.editedIndex > -1) {
        await this.actualizarTiposTransaccion(this.editedItem)
      } else {
        await this.agregarTiposTransaccion(this.editedItem)
      }
      this.snackbarOn = true
      this.close()
    },

    async actualizarTiposTransaccion(aux) {
      try {
        let header = { 'token': this.token }
        let configuracion = { headers: header }
        await axios.put(url.tiposTransaccion.urlModificar + '/' + aux.id_tipo_transaccion, aux, configuracion)
        await this.obtenerTiposTransaccion()
        this.opExitosa = true
      } catch (error) {
        console.log("Error GET: " + error)
      }
    },

    async agregarTiposTransaccion(aux) {
      try {
        let header = { 'token': this.token }
        let configuracion = { headers: header }
        await axios.post(url.tiposTransaccion.urlAgregar, aux, configuracion)
        await this.obtenerTiposTransaccion()
        this.opExitosa = true
      } catch (error) {
        console.log("Error GET: " + error)
      }
    },

  }

}


