import { mapState, mapActions } from 'vuex'
import axios from 'axios'
import url from '../../../urls/index.js'

export default {
  name: 'numeradores',
  components: {},
  props: [],
  data() {
    return {
      valid: false,
      snackbarOn: false,
      opExitosa: false,
      reglasDescripcion: [
        value => !!value || 'El campo Descripción es requerido!'],
      reglasID: [
        value => (!isNaN(parseFloat(value)) && value > 0 && value <= 9999) || 'Id Numerador debe ser un número entre 1 y 9999',
        value => !this.listaNumeradores.find(numerador=> numerador.id_numerador == value) || `El ID ${value} ya fue utilizado`,
        value => !!value || 'El campo Id Numerador es requerido!',
      ],
      headers: [
        {
          text: "Id-Numerador",
          value: "id_numerador"
        },
        {
          text: "Descripción",
          value: "descripcion"
        },
        {
          text: "Valor",
          value: "valor"
        },
        {
          text: 'Aux 1',
          value: 'aux1',
          sortable: false
        },
        {
          text: 'Aux 2',
          value: 'aux2',
          sortable: false
        },
        {
          text: 'Acciones',
          value: 'actions',
          sortable: false
        }
      ],
      itemsPorPagina: 10,
      dialog: false,
      editedIndex: -1,
      editedItem: {
        id_numerador: '',
        descripcion: '',
        valor: null,
        aux1: '',
        aux2: '',
      },
      defaultItem: {
        id_numerador: '',
        descripcion: '',
        valor: null,
        aux1: '',
        aux2: '',
      }
    }
  },

  computed: {

    colorSnackbar() {
      return this.opExitosa ? 'success' : 'error'
    },

    ...mapState('auth', ['token']),
    ...mapState('adminTipos', ['listaNumeradores']),

    formTitle() {
      return this.editedIndex === -1 ? 'Agregar Numerador' : 'Editar Numerador'
    },

  },


  watch: {
    dialog(val) {
      val || this.close()
    },
  },

  mounted() {
    this.obtenerNumeradores()
  },

  methods: {
    ...mapActions('adminTipos', ['obtenerNumeradores']),

    cerrarSnackbar() {
      this.snackbarOn = false,
        this.opExitosa = false
    },

    editItem(item) {
      this.editedIndex = this.listaNumeradores.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })

    },

    async save() {
      if (this.editedIndex > -1) {
        await this.actualizarNumerador(this.editedItem)
      } else {
        await this.agregarNumerador(this.editedItem)
      }
      this.snackbarOn = true
      this.close()
    },

    async actualizarNumerador(aux) {
      try {
        let header = { 'token': this.token }
        let configuracion = { headers: header }
        await axios.put(url.numeradores.urlModificar + '/' + aux.id_numerador, aux, configuracion)
        await this.obtenerNumeradores()
        this.opExitosa = true
      } catch (error) {
        console.log("Error GET: " + error)
      }
    },

    async agregarNumerador(aux) {
      try {
        let header = { 'token': this.token }
        let configuracion = { headers: header }
        await axios.post(url.numeradores.urlAgregar, aux, configuracion)
        await this.obtenerNumeradores()
        this.opExitosa = true
      } catch (error) {
        console.log("Error GET: " + error)
      }
    },

  }

}




